.button--tertiary {
  border: 0;
  color: #70a400;
  outline: 0;
  border-radius: 0;
  padding: 0;
}

.button--tertiary:visited {
  border: 0;
  color: #70a400;
}

.button--tertiary .button--border {
  border-bottom: 0.0625rem solid #e5e5e5;
  display: inline-block;
  padding-bottom: 0.0625rem;
}

.button--tertiary.is-hover, .button--tertiary:hover {
  border: 0;
  color: #598300;
}

.button--tertiary.is-hover .button--border, .button--tertiary:hover .button--border {
  border-bottom: 0.0625rem solid #598300;
}

.button--tertiary.is-focus, .button--tertiary:focus {
  border: 0;
  color: #598300;
}

.button--tertiary.is-focus .button--border, .button--tertiary:focus .button--border {
  border-bottom: 0.125rem dotted #598300;
  padding-bottom: 0;
}

.button--tertiary.is-active, .button--tertiary:active {
  border: 0;
  color: #598300;
}

.button--tertiary.is-active .button--border, .button--tertiary:active .button--border {
  border-bottom: 0.125rem solid #598300;
  padding-bottom: 0;
}

.button--tertiary.is-disabled, .button--tertiary:disabled {
  color: #b2b2b2;
  cursor: default;
  pointer-events: none;
}

.button--tertiary.is-disabled:focus, .button--tertiary.is-disabled:visited, .button--tertiary.is-disabled:hover, .button--tertiary.is-disabled:active, .button--tertiary:disabled:focus, .button--tertiary:disabled:visited, .button--tertiary:disabled:hover, .button--tertiary:disabled:active {
  outline: 0;
}

.button--tertiary.is-disabled .button--border, .button--tertiary:disabled .button--border {
  border-bottom: 0.0625rem solid #b2b2b2;
}

.button--tertiary.is-disabled .icon, .button--tertiary:disabled .icon {
  color: #b2b2b2;
}

.button--tertiary .icon {
  margin-right: 0.375rem;
  position: relative;
  right: 0;
  top: 0.3125rem;
}
